import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
// import store, {changeName, increase} from './store.js'
import { addCount, decreaseCount, deleteItem, sortName } from "../store.js";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";



function Cart() {

    // Redux  state  
    const { user: { name, age }, cart } = useSelector((state) => state);

    // dispatch  store.js   
    let dispatch = useDispatch();

    const smallProdcuctStyle = {
        border: "1px solid #ddd",
        width: "100px",
        height: "80px",
        cursor: "pointer",
    };

    let textverticalAlign = {
        verticalAlign: "middle",
        textAlign: "center",
    };

    return (
        <>
            <div class="container">
                <div class="row">
                    <div class="col-sm-12" style={{ textAlign: "center" }}>
                        {/*     */}
                        <h5 style={{ padding: "50px" }}>
                            {/* {name} {age} / */}
                            {name} 
                        </h5>

                        <Table>
                            <thead>
                                <tr>
                                    <th>id</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {/*      */}
                                {cart.map(({ id, imgurl, name, count }, i) => (
                                    <tr key={i}>
                                        <td style={textverticalAlign}>{id}</td>
                                        {/*         */}
                                        <td>
                                            <Link to={`/detail/${id}`}>
                                                <img src={`img/${imgurl}`} style={smallProdcuctStyle} />
                                            </Link>

                                        </td>
                                        {/* ,   */}
                                        <td style={textverticalAlign}>{name}</td>
                                        <td style={textverticalAlign}>{count}</td>

                                        <td style={textverticalAlign}>
                                            {/*   +  */}
                                            <Button onClick={() => { dispatch(addCount(id)); }} variant="outline-success" style={{ marginRight: "10px" }}> + </Button>

                                            {/*   -  */}
                                            <Button onClick={() => { dispatch(decreaseCount(id)); }} variant="outline-warning" style={{ marginRight: "10px" }} > - </Button>

                                            {/*    */}
                                            <Button onClick={() => { dispatch(deleteItem(id)); }} variant="outline-danger" >  </Button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>


                        {/*    */}
                        <Button variant="outline-primary" onClick={() => { dispatch(sortName()); }} >  </Button>{" "}
                    </div>
                </div>
            </div>
        </>
    );
}

export default Cart;
